import { makeStyles } from '@mui/styles';
import Background from '../img/fantasybg.png'

export default makeStyles((theme) => ({ 
    blkBG: {
        background: 'black',
    },

    whiteBG: {
        background: 'white'
    },

    liteGrayBG: {
        background: 'lightGray'
    },

    titleBG: {
        backgroundImage: `url(${Background})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        opacity: 0.75,
    },

    home: {
        width: '100%',
        minHeight: '90vh'
    },

    splashText: {
    },

    centerDiv: {
        display: 'block',
        margin: 'auto'
    },

    width70: {
        width: '70%'
    },

    centerText: {
        textAlign: 'center'
    },

    topPad50: {
        paddingTop: '50px'
    },

    btmPad50: {
        paddingBottom: '50px'
    },

    logo: {
        maxWidth: '200px',
        paddingRight: '10%',
        marginTop: '10px',
        marginBottom: '10px'
    },

    EKTitle: {
        display: 'block',
        width: '80%',
        margin: 'auto'
    },

    emailSignUp: {
        width: '30%'
    },
    
    subBtn: {
        height: '4em'
    },

    audioPlayer: {
        height: '240',
        width: '70%',
        border: 'none',
        minWidth: 'min(100%, 430px)'
    },

    flex: {
        display: 'flex',
        alignItems: 'center',
        flexDirection: 'row',
        justifyContent: 'center'
    },

    footer: {
        position: 'absolute',
        bottom: '0',
        width: '100%',
        height: '10vh'
    },

    listen: {
        width: '70%',
        margin: 'auto'
    },

    episodeCard: {
        display: 'block',
        margin: 'auto',
        width: '100%',
        padding: '2rem'
    },

    episodeTitle: {
        margin: 'auto',
        paddingBottom: '1rem'
    },

    epAccordion: {
        display: 'block',
        margin: 'auto',
        marginTop: '1rem'
    },
    
    epAudioPlayer: {
        height: '240',
        border: 'none',
        width: '100%'
    },

    divider: {
        borderTop: '8px solid #bbb',
        borderRadius: '5px',
        marginTop: '20px'
    },

    fanArtLogo: {
        display: 'block',
        margin: 'auto',
        height: '500px'
    }
}))